import React from "react";
import {
  useReactTable,
  getCoreRowModel,
  getFilteredRowModel,
  filterFns,
} from "@tanstack/react-table";
import { TextColumnFilter } from "./helpers/TextColumnFilter";
import { SelectColumnFilter } from "./helpers/SelectColumnFilter";
import { mockData } from "./mockedData/data";
import styles from "./Table.module.css";

const columns = [
  {
    accessorKey: "status",
    header: "Status",
    cell: (info) => info.getValue(),
    filterFn: filterFns.equals,
  },
  {
    accessorKey: "date",
    header: "Date",
    cell: (info) => info.getValue(),
  },
  {
    accessorKey: "todo",
    header: "Todo",
    cell: (info) => info.getValue(),
  },
  {
    accessorKey: "requester",
    header: "Requester",
    cell: (info) => info.getValue(),
  },
  {
    accessorKey: "executor",
    header: "Executor",
    cell: (info) => info.getValue(),
  },
  {
    accessorKey: "department",
    header: "Department",
    cell: (info) => info.getValue(),
    filterFn: filterFns.equals,
  },
  {
    accessorKey: "responsible",
    header: "Responsible",
    cell: (info) => info.getValue(),
  },
];

const TableComponent = () => {
  const table = useReactTable({
    data: mockData,
    columns,
    getCoreRowModel: getCoreRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    initialState: {
      columnFilters: [],
    },
  });

  return (
    <table className={styles.table}>
      <thead>
        {table.getHeaderGroups().map((headerGroup) => (
          <tr key={headerGroup.id}>
            {headerGroup.headers.map((header) => (
              <th key={header.id} className={`${styles.th}`}>
                {header.isPlaceholder ? null : header.column.columnDef.header}
                {header.column.getCanFilter() ? (
                  <div style={{ marginTop: "0.5rem" }}>
                    {header.column.id === "status" ||
                    header.column.id === "department" ? (
                      <SelectColumnFilter
                        column={header.column}
                        options={Array.from(
                          new Set(mockData.map((row) => row[header.column.id]))
                        )}
                      />
                    ) : (
                      <TextColumnFilter column={header.column} />
                    )}
                  </div>
                ) : null}
              </th>
            ))}
          </tr>
        ))}
      </thead>
      <tbody>
        {table.getRowModel().rows.map((row, rowIndex) => (
          <tr
            key={row.id}
            className={rowIndex % 2 === 0 ? styles.alternateRow : ""}
          >
            {row.getVisibleCells().map((cell) => (
              <td key={cell.id} className={styles.td}>
                {cell.getValue()}
              </td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default TableComponent;
